import { concatValues } from './utilities/concat-strings'

/**
 * Cria um elemento HTMLStyleElement vinculando seu CSS ao mesmo.
 * Caso voc adicione algo dinmico por meio de variveis JS / TS
 * tambm  posicionada no seu devido local.
 *
 * @export
 * @param {TemplateStringsArray} strings
 * @param {...unknown[]} values
 * @returns {HTMLStyleElement}
 */
export function css(
  strings: TemplateStringsArray,
  ...values: unknown[]
): HTMLStyleElement {
  const style = document.createElement('style')

  style.textContent = concatValues(strings, values)

  return style
}
